import { useState, useEffect, useCallback } from "react"
import { ITetrimino } from "./tetriminos"
import {
  Dimensions,
  CoordinateDictionary,
  CoordinateIndex,
  Coordinate,
} from "../types"
import { positionTetrimino, tetriminoToCoords } from "./tetris"

export const useCoordinates = (
  activeTetrimino: ITetrimino,
  brickPosition: Coordinate,
  dimensions: Dimensions
) => {
  const [stationaryBrickCoordinates, setStationaryBrickCoordinates] = useState<
    CoordinateDictionary
  >({})
  const [
    stationaryBrickCoordinateIndex,
    setStationaryBrickCoordinateIndex,
  ] = useState<CoordinateIndex>({})

  const [allCoordinates, setAllCoordinates] = useState<CoordinateDictionary>({})

  const resetCoordinates = useCallback(() => {
    setStationaryBrickCoordinates({})
    setStationaryBrickCoordinateIndex({})
  }, [])

  useEffect(() => {
    const tetrimino = positionTetrimino(activeTetrimino, brickPosition)

    setAllCoordinates({
      ...stationaryBrickCoordinates,
      ...tetriminoToCoords(tetrimino),
    })
  }, [activeTetrimino, stationaryBrickCoordinates, brickPosition])
  
  const addStationaryCoordinates = useCallback(() => {
    const tetrimino = positionTetrimino(activeTetrimino, brickPosition)
    const [height, width] = dimensions

    let newCoordinateDict = {
      ...stationaryBrickCoordinates,
      ...tetriminoToCoords(tetrimino),
    }
    let newCoordinateIndex = { ...stationaryBrickCoordinateIndex }

    tetrimino.coordinates.forEach(([posY, posX]) => {
      if (!newCoordinateIndex[posY]) {
        newCoordinateIndex[posY] = {}
      }
      newCoordinateIndex[posY][posX] = true
    })

    let rowsRemoved = 0
    // delete full rows
    {
      const pointsByRow = Object.entries(newCoordinateIndex)
      pointsByRow.forEach(([posY, row]) => {
        const pointsInRow = Object.keys(row)
        if (pointsInRow.length >= width) {
          pointsInRow.forEach(
            posX =>
              delete newCoordinateDict[String([Number(posY), Number(posX)])]
          )
          delete newCoordinateIndex[posY]
          rowsRemoved = rowsRemoved + 1
        }
      })
    }

    //shift rows down
    if (rowsRemoved) {
      let workingRow = height - 1
      const pointsByRow = Object.entries(newCoordinateIndex).sort(
        ([a], [b]) => Number(b) - Number(a)
      )
      const [index, dict] = pointsByRow.reduce(
        ([index, dict], [posY, points]) => {
          index[workingRow] = points

          Object.keys(points).forEach(([posX]) => {
            dict[String([workingRow, Number(posX)])] =
              newCoordinateDict[String([Number(posY), Number(posX)])]
          })

          workingRow = workingRow - 1

          return [index, dict]
        },
        [{}, {}]
      )
      newCoordinateIndex = index
      newCoordinateDict = dict
    }

    setStationaryBrickCoordinates(newCoordinateDict)
    setStationaryBrickCoordinateIndex(newCoordinateIndex)

    return rowsRemoved
  }, [
    stationaryBrickCoordinates,
    stationaryBrickCoordinateIndex,
    activeTetrimino,
    brickPosition,
  ])

  return {
    resetCoordinates,
    allCoordinates,
    addStationaryCoordinates,
    stationaryBrickCoordinates,
  }
}
