import { useState, useCallback, useEffect } from "react"

export const useTiming = (
  moveDown,
  getNextTetrimino,
  resetCoordinates,
  addStationaryCoordinates,
  score,
  setScore,
  addToScore
) => {
  const [gameTicker, setGameTicker] = useState(0)
  const [speed, setSpeed] = useState(40)

  const increaseSpeed = useCallback(
    (rowsRemoved: number) => {
      setSpeed(speed - 2 * rowsRemoved)
    },
    [speed]
  )

  const resetGameSpeed = useCallback(() => {
    setSpeed(40)
  }, [])

  const onLand = useCallback(
    (cb?: Function) => {
      const rowsRemoved = addStationaryCoordinates()

      if (rowsRemoved) {
        increaseSpeed(rowsRemoved)
        addToScore(rowsRemoved)
      }
      getNextTetrimino()
    },
    [addStationaryCoordinates, increaseSpeed, getNextTetrimino, addToScore]
  )

  const onOutOfBounds = useCallback(() => {
    window.alert(`You've lost! Your final score was ${score}`)
    resetCoordinates()
    setScore(0)
    resetGameSpeed()
    getNextTetrimino()
  }, [getNextTetrimino, score])
  useEffect(() => {
    if (!(gameTicker % speed)) {
      moveDown(onLand, onOutOfBounds)
    }
  }, [gameTicker, speed])

  useEffect(() => {
    setTimeout(() => setGameTicker(gameTicker + 1), 10)
  }, [gameTicker])
  return {
    speed,
    resetGameSpeed,
  }
}
