import React, { useState, useEffect, useCallback } from "react"

import { useTetriminos, ITetrimino } from "../hooks/tetriminos"
import { Coordinate, CoordinateDictionary } from "../types"
import { useCoordinates } from "../hooks/coordinates"
import { useMovement } from "../hooks/movement"
import { useBindings } from "./bindings"
import { useTiming } from "./timing"

export const shiftCoordinate = (
  coordinate: Coordinate,
  shift: Coordinate
): Coordinate => [coordinate[0] + shift[0], coordinate[1] + shift[1]]

export const shiftCoordinates = (
  coordinates: Coordinate[],
  shift: Coordinate
): Coordinate[] => {
  return coordinates.map(coordinate => {
    return shiftCoordinate(coordinate, shift)
  })
}

export const positionTetrimino = (
  tetrimino: ITetrimino,
  brickPosition: Coordinate
) => {
  return {
    ...tetrimino,
    coordinates: shiftCoordinates(tetrimino.coordinates, brickPosition),
  }
}
export const tetriminoToCoords = (tetrimino: ITetrimino) =>
  tetrimino.coordinates.reduce((dict, coordinate) => {
    dict[String(coordinate)] = tetrimino.color
    return dict
  }, {} as CoordinateDictionary)

export const useTetris = (initialDimensions: [number, number]) => {
  const [dimensions, setDimensions] = useState(initialDimensions)

  const {
    activeTetrimino,
    setActiveTetrimino,
    nextTetrimino,
    replaceCurrentTetrimino,
  } = useTetriminos(dimensions)

  const defaultBrickPosition: Coordinate = [
    0,
    Math.floor(dimensions[1] / 2 - 1),
  ]
  const [brickPosition, setBrickPosition] = useState<Coordinate>(
    defaultBrickPosition
  )
  const gotoDefaultBrickPosition = useCallback(() => {
    setBrickPosition(defaultBrickPosition)
  }, [])

  const {
    allCoordinates,
    resetCoordinates,
    stationaryBrickCoordinates,
    addStationaryCoordinates,
  } = useCoordinates(activeTetrimino, brickPosition, dimensions)

  const {
    moveDown,
    moveLeft,
    moveRight,
    rotateClockwise,
    rotateCounterClockwise,
  } = useMovement(
    activeTetrimino,
    setActiveTetrimino,
    brickPosition,
    setBrickPosition,
    dimensions,
    stationaryBrickCoordinates
  )

  const getNextTetrimino = useCallback(() => {
    replaceCurrentTetrimino()
    gotoDefaultBrickPosition()
  }, [addStationaryCoordinates, replaceCurrentTetrimino])
  // BUG: removing multiple rows is spotty

  const [score, setScore] = useState(0)

  const addToScore = useCallback(
    (rowsDeleted: number) => {
      setScore(score + rowsDeleted * (100 * rowsDeleted))
    },
    [score]
  )

  const { speed, resetGameSpeed } = useTiming(
    moveDown,
    getNextTetrimino,
    resetCoordinates,
    addStationaryCoordinates,
    score,
    setScore,
    addToScore
  )
  const resetGameState = useCallback(() => {
    resetGameSpeed()
    resetCoordinates()
    setScore(0)
    getNextTetrimino()
  }, [resetCoordinates, resetGameSpeed, getNextTetrimino])

  const bindings = [
    { key: "ArrowDown", fn: moveDown },
    { key: "ArrowLeft", fn: moveLeft },
    { key: "ArrowRight", fn: moveRight },
    { key: "w", fn: rotateClockwise },
    { key: "q", fn: rotateCounterClockwise },
  ]

  useBindings(bindings)

  return {
    dimensions,
    resetGameState,
    activeTetrimino,
    tetriminoToCoords,
    score,
    nextTetrimino,
    setDimensions,
    replaceCurrentTetrimino,
    allCoordinates,
    shiftCoordinates,
    setBrickPosition,
    addStationaryCoordinates,
    moveLeft,
    moveRight,
    speed,
    moveDown,
    rotateClockwise,
    rotateCounterClockwise,
  }
}
